// Buttons
// =======
//
// This component enhances the button styles on Siemens application pages.
//
// Vertically space out multiple block buttons
//
//
// <usage class="clearfix">
//   <div class="form-group">
//     <div class="col-lg-offset-3 col-lg-3 col-sm-4"><button class="btn btn-primary btn-block" type="submit">Save</button></div>
//     <div class="col-lg-3 col-sm-4"><a href="#" class="btn btn-primary btn-block">Cancel</a></div>
//     <div class="col-lg-3 col-sm-4"><a href="#" class="btn btn-primary btn-block">Reset</a></div>
//   </div>
// </usage>

@include si-button('primary', $si-primary-hover);
@include si-button-outline('primary', $si-primary-hover);


.btn {
  &:focus {
    // override bootstrap btn focus
    /* stylelint-disable-next-line declaration-no-important */
    outline: 1px solid $si-outline-color !important;
    outline-offset: 2px;
    box-shadow: none;
  }

  &.btn-link {
    padding: 0;
  }

  &.disabled,
  &[disabled] {
    pointer-events: none;
  }
}

.btn-free {
  color: $primary;

  &:hover,
  &:focus {
    color: $si-primary-hover;
    background-color: $si-button-default-background;
  }

  &:active {
    color: $si-interactive-blue-light-pressed;
    background-color: $si-button-default-background;
  }
}

.btn-default {
  @extend .btn-free;
  border-color: $primary;

  &:hover,
  &:focus {
    border-color: $si-primary-hover;
  }

  &:active {
    border-color: $si-interactive-blue-light-pressed;
  }
}

.btn-hero {
  /* stylelint-disable-next-line declaration-no-important */
  color: $si-deep-blue !important;
  background: $si-bold-petrol-gradient;
  background-color: $si-bold-green;

  &:hover {
    background: $si-bold-green;
  }

  &:active {
    background: $si-bold-blue;
  }
}
