@import 'bootstrap/scss/tables';

th {
  font-weight: $table-header-font-weight;
}

.table {
  border-collapse: $table-border-collapse;
  font-size: $si-table-font-size;
  border-spacing: 20px 0;
  margin-left: -20px;
  margin-right: -20px;

  > thead {
    > tr {
      > th,
      > td {
        border: 0;
      }

      > th {
        white-space: nowrap;
        position: sticky;
        top: 0;
        z-index: 3;

        .form-group {
          width: 100%;
          height: 100%;
          margin-bottom: 0;

          label {
            top: 0;
            left: 0;
            right: 0;
            cursor: auto;
            color: $table-color;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-weight: inherit;

            &.selected {
              font-size: 9px;
              top: -16px;
            }
          }

          .form-control {
            padding: 0;
            background: none;
            border: 0;
            box-shadow: none;
            height: 24px;
            font-weight: normal;
            text-align: left;
          }
        }

        .action-item-group {
          .action {
            display: inline-flex;
            flex-flow: column;
            text-decoration: none;
            justify-content: center;
            line-height: 1;
            min-width: 24px;
            height: 24px;

            &:focus {
              outline: none;
              box-shadow: none;
            }

            &.toggle-sort {
              span {
                font-size: 24px;

                &.desc {
                  margin-top: -6px;
                }

                &.asc {
                  margin-bottom: -6px;
                }
              }

              &.desc {
                span.asc {
                  opacity: 0.5;
                }
              }

              &.asc {
                span.desc {
                  opacity: 0.5;
                }
              }
            }
          }
        }
      }

      > td:first-child {
        border: 0;
      }
    }
  }

  > caption + thead,
  > colgroup + thead,
  > thead:first-child {
    > tr:first-child {
      > th,
      > td {
        border-top: 1px solid $table-header-border;
      }
    }
  }

  > tbody > tr {
    > td {
      vertical-align: inherit;
      word-wrap: break-word;
      transition-property: background-color, border-color;
      transition-duration: 0.15s;
      transition-timing-function: $si-fluid-easing-function;
    }

    &:hover {
      td {
        background-color: $table-hover-bg;
        border-color: $table-border-hover-color;
      }

      + tr td {
        border-top-color: $table-border-hover-color;
      }
    }

    &:last-child {
      &:hover {
        > td {
          border-color: $table-border-hover-color;
        }
      }

      > td {
        border-bottom: 1px solid $table-border-color;
      }
    }
  }

}

.table-bordered {
  border: $table-bordered-border;
}

.loader {
  position: absolute;
  height: 2px;
  left: 0;
  right: 0;
  transform: translate(0, 2px) scale(1, 2);
  background-color: $si-table-loading-bg;

  .loader-bar {
    animation: loading-table 0.8s infinite cubic-bezier(0.39, 0.575, 0.565, 1);
    background-color: $si-table-loading-color;
    position: absolute;
    height: 2px;
    width: 100%;
  }
}

// Progress bar animations
@keyframes loading-table {
  0% {
    opacity: 0;
    transform: translateX(-50%) scale(0.3, 1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: translateX(50%) scale(0, 1);
  }
}

@include media-breakpoint-down(sm) {
  .table {
    border-spacing: 0 0;
    margin: 0;

    > tbody > tr,
    > thead > tr,
    > tr {
      display: flex;
      flex-flow: column;

      > td,
      > th {
        display: flex;
      }
    }

    th,
    td {
      // stylelint-disable-next-line declaration-no-important
      border: 0 !important;
    }

    tbody > tr {
      border-top: $table-border-width solid $table-border-color;

      &:last-child {
        border-bottom: $table-border-width solid $table-border-color;
      }
    }
  }
}
