.si-footer {
  width: 100%;
  padding-top: 55px;
  padding-bottom: 55px;

  > .container,
  > .container-fluid {
    display: grid;
    align-items: center;
  }

  .si-copyright-legal-links {
    display: flex;
    flex-flow: column;
  }


  .si-locale-application-name {
    display: flex;
    flex-flow: column;
  }

  .si-icons {
    font-size: 20px;
    display: flex;
    gap: 15px;
  }

  .si-contact-us {
    justify-self: left;
  }

  .nav-link {
    &:hover {
      text-decoration: underline;
    }
  }

  .nav-item {
    &:first-child {
      .nav-link {
        padding-left: 0;
      }
    }

    &:last-child {
      .nav-link {
        padding-right: 0;
      }
    }
  }
}

@include media-breakpoint-down(md) {
  .si-footer {
    .nav-link,
    .si-copyright,
    .si-application-name {
      /* stylelint-disable-next-line declaration-no-important */
      padding: 15px 0 !important;
    }

    .si-contact-us {
      margin-top: 15px;
    }
  }
}

@include media-breakpoint-up(lg) {
  .si-footer {
    > .container,
    > .container-fluid {
      grid-gap: $grid-gutter-width;
    }

    &.si-footer-legal-only {
      > .container,
      > .container-fluid {
        grid-template-columns: 1fr auto;
        grid-template-areas: 'application-name application-name' 'copyright legal-links';
      }
    }

    &.si-footer-intranet {
      > .container,
      > .container-fluid {
        grid-template-columns: 1fr auto;
        grid-template-areas: 'copyright legal-links';
      }
    }

    &.si-footer-extended {
      > .container,
      > .container-fluid {
        grid-template-columns: 1fr auto;
        grid-template-areas: 'locale-application-name icons' 'copyright copyright' 'legal-links contact-us';
      }
    }

    .si-application-name {
      grid-area: application-name;
    }

    .si-copyright {
      grid-area: copyright;
    }

    .si-legal-links {
      grid-area: legal-links;
    }

    .si-locale {
      grid-area: locale;
    }

    .si-icons {
      grid-area: icons;
    }

    .si-contact-us {
      grid-area: contact-us;
      justify-self: right;
    }

    .si-locale-application-name {
      grid-area: locale-application-name;
      flex-flow: row;
    }
  }
}

@include media-breakpoint-up(xl) {
  .si-footer {
    &.si-footer-legal-only {
      > .container,
      > .container-fluid {
        grid-template-columns: 1fr auto auto;
        grid-template-areas: 'application-name copyright legal-links';
      }
    }

    &.si-footer-extended {
      > .container,
      > .container-fluid {
        grid-template-columns: auto 1fr auto;
        grid-template-areas: 'locale-application-name locale-application-name icons' 'copyright legal-links contact-us';
      }
    }
  }
}
