@import 'bootstrap/scss/popover';
// Popovers
// ========
// This Comonent adjust the look of the pPopover tooltips.
// Popover elements need to be enabled with a jquery popover function.
//
// <usage>
//   <a href="#" title="Title" data-toggle="popover" data-placement="top" data-content="Content">Top</a>
//   <a href="#" title="Title" data-toggle="popover" data-placement="bottom" data-content="Content">Bottom</a>
//   <a href="#" title="Title" data-toggle="popover" data-placement="left" data-content="Content">Left</a>
//   <a href="#" title="Title" data-toggle="popover" data-placement="right" data-content="Content">Right</a>
//
//   <script>
//   $(document).ready(function(){
//       $('[data-toggle="popover"]').popover();
//   });
//   </script>
// <usage>

.popover {
  border: 0;
  border-radius: 0;
  color: lighten($black, 93.5%);
  box-shadow: 0 2px 4px 0 $box-shadow-color;
  text-align: center;
}
  
.popover-header {
  color: $link-color;
  background-color: initial;
  border-bottom: 0;
}
