@mixin header-left-border($offset-vertical, $offset-left, $width) {
  position: relative;
  padding-left: $offset-left;

  &::before {
    content: ' ';
    position: absolute;
    top: $offset-vertical;
    bottom: $offset-vertical;
    left: 0;
    width: $width;
    background: $si-bold-petrol-gradient-vertical;
  }
}
