@import 'bootstrap/scss/nav';
// Navs
// ====
//
// This component extends styling possibilities for `.nav`.
.nav {
  &:hover {
    color: $nav-link-color-hover;
  }
}

// Tabs
// -------------------------

// Give the tabs something to sit on
.nav-tabs {
  width: $nav-tabs-width;
  border: 0;
  font-weight: $nav-tabs-font-weight;

  @include media-breakpoint-down(sm) {
    > .nav-link {
      flex: 1 100%;
    }
  }

  .nav-link {
    margin-bottom: 0;
    padding: $nav-tabs-link-padding;
    color: $si-nav-link-color;
    border: 0;
    border-bottom: $si-tabs-link-border-color 2px solid;
    cursor: pointer;
    text-align: $nav-tabs-text-align;
    flex: 1;

    &:hover,
    &:focus {
      background: $nav-tabs-link-hover-bg;
    }

    &.disabled {
      color: $nav-link-disabled-color;
      background-color: transparent;
      border-color: transparent;
    }
  }

  &.nav-justified {
    .nav-link.active {
      border: $nav-tabs-border-width solid $nav-tabs-border-color;
      border-bottom: 0;
    }
  }
}

.nav-justified {
  > li > a {
    text-align: $nav-justified-link-text-align;
  }

}

.tab-pane {
  padding-top: 30px;
  padding-bottom: 30px;
}

