@import 'bootstrap/scss/list-group';

.list-group-item {
  border-color: $list-group-border-color;
  border-style: solid none;
  border-width: 1px 0 0;

  &:last-child {
    border-width: 1px 0;
  }

  + .list-group-item {
    border-top-width: 1px;
  }
}

.list-group-item-action:hover {
  cursor: pointer;
  border-color: $si-list-group-border-hover-color;

  + .list-group-item {
    border-top-color: $si-list-group-border-hover-color;
  }
}

.filter-group {
  @extend .list-group;
}

.filter-group-item {
  @extend .list-group-item;
  color: $filter-group-border;
  background-color: $filter-group-bg;
  border-color: $filter-group-border;
}
