// Variables
$font-path: '../fonts' !default;
$siemens-font-path: $font-path !default;
$simpl-fonts-no-woff: false !default;

// Font Face mixin
//
// This mixin simplifies proper @font-face definitions.
@mixin font-face($family-name, $family-font, $weight, $name, $italic-name: #{$name}Italic) {
  @at-root {
    @if $simpl-fonts-no-woff {
      @font-face {
        font-family: $family-name;
        font-weight: $weight;
        font-style: normal;
        src: local('#{$family-name} #{$name}'),
          url('#{$siemens-font-path}/#{$family-font}_#{$name}.otf') format('opentype');
      }

      @font-face {
        font-family: $family-name;
        font-weight: $weight;
        font-style: italic;
        src: local('#{$family-name $name} Italic'),
          url('#{$siemens-font-path}/#{$family-font}_#{$italic-name}.otf') format('opentype');
      }

    } @else {
      @font-face {
        font-family: $family-name;
        font-weight: $weight;
        font-style: normal;
        src: local('#{$family-name} #{$name}'),
          url('#{$siemens-font-path}/#{$family-font}_#{$name}.woff2') format('woff2'),
          url('#{$siemens-font-path}/#{$family-font}_#{$name}.woff') format('woff');
      }

      @font-face {
        font-family: $family-name;
        font-weight: $weight;
        font-style: italic;
        src: local('#{$family-name $name} Italic'),
          url('#{$siemens-font-path}/#{$family-font}_#{$italic-name}.woff2') format('woff2'),
          url('#{$siemens-font-path}/#{$family-font}_#{$italic-name}.woff') format('woff');
      }
    }
  }
}
