@import 'bootstrap/scss/reboot';

*:focus {
  outline: 1px solid $si-outline-color;
  outline-offset: 2px;
}

*:active {
  // resets the outline in all browsers
  outline: none;
}


html {
  overflow-y: scroll;
  overflow-x: hidden;
  min-height: 100%;
}

body {
  min-height: 100vh;
  display: flex;
  overflow-x: hidden;
  flex-direction: column;
}

main {
  flex: 1;
}

table {
  border-spacing: 0;
}
