@import 'bootstrap/scss/type';

h1 {
  @include header-left-border(14px, 30px, 6px);
  line-height: $h1-line-height;
  letter-spacing: $h1-letter-spacing;
}

h2 {
  @include header-left-border(11px, 24px, 4px);
  line-height: $h2-line-height;
  letter-spacing: $h2-letter-spacing;
}

h3 {
  line-height: $h3-line-height;
  letter-spacing: $h3-letter-spacing;
}

h4 {
  line-height: $h4-line-height;
}

h5 {
  line-height: $h5-line-height;
}

h6 {
  line-height: $h6-line-height;
}

.blockquote {
  line-height: $blockquote-line-height;
  letter-spacing: $blockquote-letter-spacing;

  &::before {
    display: block;
    content: '\"';
    font-style: italic;
    color: $si-bold-green;
    font-size: $blockquote-font-size * 2;
    margin-bottom: -25px;
    padding-top: 20px;
  }
}

.intro {
  font-size: $intro-font-size;
  line-height: $intro-line-height;
}

.italic {
  font-style: italic;
}

.bold {
  font-weight: 900;
}

small {
  line-height: $small-line-height;
}

caption {
  font-size: $caption-font-size;
  line-height: $caption-line-height;
}
