.bs-datepicker-container {
  padding: 0;
}

.bs-datepicker-head {
  border-radius: 0;

  button {
    font-weight: 900;

    &.next,
    &.previous {
      font-weight: initial;
    }

    &:hover {
      background-color: transparent;
    }
  }
}

.bs-datepicker-body table {
  td {
    span {
      border-radius: 0;
    }

    span.selected,
    &.selected span,
    span[class*='select-'],
    &[class*='select-'] span {
      border-radius: 0;
      font-weight: 900;
    }

    span.in-range {
      font-weight: 900;
    }

    &:hover {
      font-weight: 900;
    }

  }
}
