//         <ul class="nav navbar-nav navbar-right">
//           <li class="dropdown">
//             <button class="btn btn-default dropdown-toggle" role="button" data-toggle="dropdown">
//               English
//               <span class="caret"></span>
//             </button>
//             <ul class="dropdown-menu">
//               <li><a href="#">English</a></li>
//               <li><a href="#">French</a></li>
//               <li><a href="#">German</a></li>
//             </ul>
//           </li>
//         </ul>
//       </div>
//     </div>
//   </nav>
//   <div class="page-slogan">
//     <div>
//       Lorem ipsum dolor
//       <small>Sed ut perspiciatis unde omnis iste natus error sit</small>
//     </div>
//   </div>
// </div>
// </usage>
@mixin opacity($opacity) {

  $opacity-ie: ($opacity * 100); // IE8 filter

  filter: alpha(opacity=$opacity-ie);

  opacity: $opacity;

}

.jumbotron.jumbotron-landing {
  position: relative;
  height: $jumbotron-landing-height;
  padding: 0;
  background-image: url($jumbotron-landing-image);
  background-position: center;

  .si-header {
    border-width: 0;
    z-index: $zindex-navbar;

    > .container,
    > .container-fluid {
      padding: 0;
    }

    .si-header-brand {
      margin: 0;
      height: $jumbotron-landing-brand-height;
      padding: 0 0 0 44px;
    }

    .navbar-landing {
      margin-top: 30px;
      margin-right: 30px;

      @include media-breakpoint-down(md) {
        @include opacity($jumbotron-landing-navbar-opacity);
        margin: 0;
        background-color: $jumbotron-landing-navbar-bg;
      }
    }

    .dropdown {
      color: $jumbotron-landing-dropdown-color;

      > .btn {
        width: $jumbotron-landing-dropdown-width;
        border: 1px solid $jumbotron-landing-dropdown-color;
        background-color: transparent;
        text-align: right;
        color: $jumbotron-landing-dropdown-color;

        @include media-breakpoint-down(md) {
          border-width: 0;
          text-align: left;
          color: $jumbotron-landing-dropdown-color-sm;
        }
      }

      .dropdown-menu {
        margin-top: 0;
        border: 1px solid $jumbotron-landing-dropdown-color;
        min-width: $jumbotron-landing-dropdown-width;
        text-align: right;
        background-color: transparent;
        border-top-width: 0;

        @include media-breakpoint-down(md) {
          text-align: left;
          border-width: 0;
        }
      }

      .dropdown-menu > li > a {
        color: $jumbotron-landing-dropdown-color;
        @include media-breakpoint-down(md) {
          color: $jumbotron-landing-dropdown-color-sm;
        }
      }

      .dropdown-menu > li > a:hover,
      .dropdown-menu > li > a:focus {
        //#gradient > .vertical($start-color: $dropdown-link-hover-bg; $end-color: darken($dropdown-link-hover-bg, 5%));
        background-color: fade($jumbotron-landing-dropdown-color, 30%);
      }
    }
  }

  .page-slogan,
  .page-header {
    margin: 0 0 40px;
    position: absolute;
    right: 0;
    bottom: 0;
    min-width: 50%;
    min-height: 110px;
    background-color: $jumbotron-landing-color-bg;
    padding: 30px 30px 25px 25px;
    border: 0;

    h1,
    h2,
    h3,
    > div {
      margin: 0;
      color: $jumbotron-landing-color;
      font-size: 30px;
      font-family: $font-family-base;
      line-height: 1.1;
      font-weight: 500;

      small {
        display: block;
        color: $jumbotron-landing-color;
        line-height: 1;
        font-weight: normal;
        font-family: $font-family-base;
        font-size: 16px;
        margin-top: 10px;
      }
    }
  }
}

.jumbotron {
  background: $sn-petrol-gradient;
  border-radius: 0;

  > hr {
    border: 0;
  }
}
