$pager-color: #005578;
$pager-color-active: #fff;
$pager-color-inactive: #9bafbe;
$table-border-color: rgba(0, 0, 0, 0.12);

.ngx-datatable.newton {
  background: transparent;

  &.striped {
    .datatable-row-odd {
      background: #eee;
    }
  }

  &.multi-click-selection,
  &.multi-selection,
  &.single-selection {
    .datatable-body-row {
      &.active,
      &.active .datatable-row-group {
        background-color: #304ffe;
        color: #fff;
      }

      &.active:hover,
      &.active:hover .datatable-row-group {
        background-color: #193ae4;
        color: #fff;
      }

      &.active:focus,
      &.active:focus .datatable-row-group {
        background-color: #2041ef;
        color: #fff;
      }
    }
  }

  &:not(.cell-selection) {
    .datatable-body-row {
      &:hover,
      &:hover .datatable-row-group {
        background-color: #eee;
        transition-property: background;
        transition-duration: 0.3s;
        transition-timing-function: linear;
      }

      &:focus,
      &:focus .datatable-row-group {
        background-color: #ddd;
      }
    }
  }

  &.cell-selection {
    .datatable-body-cell {
      &:hover,
      &:hover .datatable-row-group {
        background-color: #eee;
        transition-property: background;
        transition-duration: 0.3s;
        transition-timing-function: linear;
      }

      &:focus,
      &:focus .datatable-row-group {
        background-color: #ddd;
      }

      &.active,
      &.active .datatable-row-group {
        background-color: #304ffe;
        color: #fff;
      }

      &.active:hover,
      &.active:hover .datatable-row-group {
        background-color: #193ae4;
        color: #fff;
      }

      &.active:focus,
      &.active:focus .datatable-row-group {
        background-color: #2041ef;
        color: #fff;
      }
    }
  }

  // Shared Styles
  .empty-row {
    height: 50px;
    text-align: left;
    padding: 0.5rem 1.2rem;
    vertical-align: top;
    border-top: 0;
  }

  .loading-row {
    text-align: left;
    padding: 0.5rem 1.2rem;
    vertical-align: top;
    border-top: 0;
  }

  // Global Row Styles
  .datatable-body,
  .datatable-header {
    .datatable-row-left {
      background-color: #fff;
      background-position: 100% 0;
      background-repeat: repeat-y;
      background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAABCAYAAAD5PA/NAAAAFklEQVQIHWPSkNeSBmJhTQVtbiDNCgASagIIuJX8OgAAAABJRU5ErkJggg==');
    }

    .datatable-row-right {
      background-position: 0 0;
      background-color: #fff;
      background-repeat: repeat-y;
      background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAABCAYAAAD5PA/NAAAAFklEQVQI12PQkNdi1VTQ5gbSwkAsDQARLAIGtOSFUAAAAABJRU5ErkJggg==');
    }
  }

  // Header Styles
  .datatable-header {
    border-top: 1px solid $table-border-color;
    border-bottom: 1px solid $table-border-color;

    .datatable-header-cell {
      text-align: left;
      padding: 0.5rem 1.2rem;
      font-weight: 500;
      vertical-align: bottom;

      .datatable-header-cell-wrapper {
        position: relative;
      }

      &.longpress {
        .draggable::after {
          transition: transform 400ms ease, opacity 400ms ease;
          opacity: 0.5;
          transform: scale(1);
        }
      }

      .draggable::after {
        content: ' ';
        position: absolute;
        top: 50%;
        left: 50%;
        margin: -30px 0 0 -30px;
        height: 60px;
        width: 60px;
        background: #eee;
        border-radius: 100%;
        opacity: 1;
        filter: none;
        transform: scale(0);
        z-index: 9999;
        pointer-events: none;
      }

      &.dragging {
        .resize-handle {
          border-right: 0;
        }
      }
    }

    .resize-handle {
      border-right: solid 1px #eee;
    }
  }

  // Body Styles
  .datatable-body {
    .datatable-row-detail {
      background: #f5f5f5;
      padding: 10px;
    }

    .datatable-body-row {
      border-bottom: 1px solid $table-border-color;

      .datatable-body-cell {
        text-align: left;
        padding: 0.5rem 1.2rem;
        vertical-align: top;
        border-top: 0;

        transition: width 0.3s ease;

        // cell active class
        // &.active {
        //  background: #0829e0
        // }
      }
    }

    .progress-linear {
      display: block;
      position: relative;
      width: 100%;
      height: 5px;
      padding: 0;
      margin: 0;
      position: absolute;

      .container {
        display: block;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 5px;
        transform: translate(0, 0) scale(1, 1);
        background-color: rgb(170, 209, 249);

        .bar {
          transition: all 0.2s linear;
          animation: query 0.8s infinite cubic-bezier(0.39, 0.575, 0.565, 1);

          transition: -webkit-transform 0.2s linear;
          transition: transform 0.2s linear;
          background-color: rgb(16, 108, 200);

          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          width: 100%;
          height: 5px;
        }
      }
    }
  }

  // Footer Styles
  .datatable-footer {
    // border-top: 1px solid rgba(0, 0, 0, 0.12);

    font-size: 14px;

    .page-count {
      line-height: 50px;
      height: 50px;
      padding: 0 1.2rem;
    }

    .datatable-pager {
      margin: 0 10px;
      // line-height: 50px;
      // height: 50px;
      // vertical-align: middle;

      li {
        vertical-align: middle;

        &.disabled a {
          color: $pager-color-inactive !important; // stylelint-disable-line
          background-color: transparent !important; // stylelint-disable-line
        }

        &.active a {
          background-color: $pager-color;
          color: $pager-color-active;
          font-weight: normal;
        }
      }

      a {
        height: 20px;
        min-width: 20px;
        line-height: 20px;
        padding: 0;
        margin: 0 2px;
        border-radius: 0;
        border-style: none;
        text-align: center;
        vertical-align: top;
        vertical-align: bottom;
        color: $pager-color;
        text-decoration: none;

        &:hover {
          color: $pager-color-active;
          background-color: $pager-color;
        }
      }

      .icon-left,
      .icon-prev,
      .icon-right,
      .icon-skip {
        font-size: 20px;
        line-height: 20px;
        padding: 0;
      }
    }
  }
}

// Checkboxes
.datatable-checkbox {
  position: relative;
  margin: 0;
  cursor: pointer;
  vertical-align: middle;
  display: inline-block;
  box-sizing: border-box;
  padding: 10px 0;

  input[type='checkbox'] {
    position: relative;
    margin: 0 1rem 0 0;
    cursor: pointer;
    outline: none;

    &::before {
      transition: all 0.3s ease-in-out;
      content: '';
      position: absolute;
      left: 0;
      z-index: 1;
      width: 1rem;
      height: 1rem;
      border: 2px solid #f2f2f2;
    }

    &:checked::before {
      transform: rotate(-45deg);
      height: 0.5rem;
      border-color: #009688;
      border-top-style: none;
      border-right-style: none;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1rem;
      height: 1rem;
      background: #fff;
      cursor: pointer;
    }
  }
}

// Progress bar animations
@keyframes query {
  0% {
    opacity: 1;
    transform: translateX(35%) scale(0.3, 1);
  }

  100% {
    opacity: 0;
    transform: translateX(-50%) scale(0, 1);
  }
}

@import '~@simpl/siemens-brand-bootstrap/src/styles/siemens-variables';


.ng-invalid.ng-touched {
  @extend .is-invalid !optional;
}

.si-invalid {
  label {
    color: $brand-danger;
  }

  .invalid-feedback {
    display: block;

    &:empty {
      display: none;
    }
  }
}

.input-group {
  width: 100%;
}

.form-group label {
  z-index: 4;
}

.platform-server {
  ul.navbar-primary {
    > li.server-navbar-underline,
    > li:hover {
      > a::after {
        content: ' ';
        border-bottom: 4px solid #099;
        position: absolute;
        width: calc(100% - 20px);
        left: 0;
        bottom: -3px;
      }
    }
  }
}
