@import 'bootstrap/scss/input-group';

.input-group {
  width: 100%;

  > .input-group {
    flex: 1 1;
  }
}

.input-group-append,
.input-group-prepend,
.input-group-text {
  min-width: $form-control-height;
  text-align: center;
  justify-content: center;
}

.form-group .action-item-group {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  z-index: 3;

  .action-item {
    padding-right: 17px;
  }
}
