@import 'bootstrap/scss/card';


.card {
  .card-title {
    @include header-left-border(7px, 15px, 3px);
  }
}

.card-img-overlay {
  top: auto;
}

.accordion {
  > .card {
    background-color: transparent;
    overflow: visible;
    border-bottom: initial;
    border-top: 1px solid $si-card-accordion-color;
    transition-property: background-color, z-index, border-top-color;
    transition-duration: 0.15s;
    transition-timing-function: $si-fluid-easing-function;

    &:hover {
      z-index: 1;
      background-color: $si-card-accordion-hover-background-color;
      border-top-color: $si-card-accordion-hover-border-color;
      border-bottom: $card-border-width solid $si-card-accordion-hover-border-color;
      margin-bottom: -$card-border-width;

      + .accordion {
        border-top: 0;
      }
    }

    > .card-header {
      display: flex;
      justify-content: space-between;
      background-color: transparent;
      font-weight: 900;
      padding: 1.25rem 0.9375rem 1.25rem 20px;
      cursor: pointer;
      border-bottom: 0;

      &::after {
        @include add-icon($siemens-plus);
      }

      .card-title {
        display: inline-block;
        margin: 0;
        position: relative;
        font-size: 20px;
        width: 100%;
        text-align: left;
        box-shadow: none;
        line-height: 26px;
        opacity: 0.8;

        &:hover,
        &:active {
          opacity: 1;
        }
      }

      .btn-link {
        font-weight: 900;
        font-size: 20px;
        color: $si-card-accordion-color;
        text-decoration: none;
      }
    }

    > .card-header.collapsed {
      .card-title::after {
        transform: rotate(0deg);
      }
    }

    > .card-body {
      padding: 1.25rem 0 2.5rem;
    }
  }

  &:last-child {
    > .card {

      border-bottom: $card-border-width solid $si-card-accordion-color;

      &:hover {
        border-bottom: $card-border-width solid $si-card-accordion-hover-border-color;
      }
    }
  }

  &.open,
  &.panel-open {
    .card-header::after {
      @include add-icon($siemens-minus);
    }
  }
}

accordion-group {
  @extend .accordion;
}
