@import 'bootstrap/scss/dropdown';

.dropdown-menu > li {
  &.selected {
    background-color: $dropdown-link-active-bg;
  }
}

.dropdown-menu.multi > li > a {
  &::before {
    @include add-icon($siemens-check);
    padding: 0 0 0 1px !important; // stylelint-disable-line
    font-size: 14px;
    color: $si-form-check-background;
    background-color: $si-form-check-background;
    border: 1px solid $input-border-color;
    width: 21px;
    display: inline-flex;
    height: 21px;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
  }

  &.checked::before {
    color: $si-form-check-color;
  }
}

.dropdown-header {
  background: $dropdown-header-bg;
  border-bottom: 0;
}

.si-dropdown-menu-standalone {
  border-top: 1px solid $si-dropdown-standalone-border;
  margin-top: 10px + $si-dropdown-triangle-height;
  color: $body-color;
  padding: 20px;
  // stylelint-disable-next-line declaration-no-important
  width: 280px !important;
}

.si-dropdown-border-loader {
  position: absolute;
  height: 1px;
  background: $dropdown-bg;
  top: -1px;
  right: 0;
  width: 0;
}

.si-triangle-top {
  border-left: $si-dropdown-triangle-height solid transparent;
  border-right: $si-dropdown-triangle-height solid transparent;
  border-bottom: $si-dropdown-triangle-height solid $si-dropdown-standalone-border;
  position: absolute;
  top: -$si-dropdown-triangle-height;
  left: 12px;

  &::after {
    content: '';
    width: 0;
    height: 0;
    border-bottom: $si-dropdown-triangle-height - $si-dropdown-triangle-border-width solid $dropdown-bg;
    border-left: $si-dropdown-triangle-height - $si-dropdown-triangle-border-width solid transparent;
    border-right: $si-dropdown-triangle-height - $si-dropdown-triangle-border-width solid transparent;
    position: absolute;
    top: $si-dropdown-triangle-border-width;
    left: -$si-dropdown-triangle-height + $si-dropdown-triangle-border-width;
  }
}
