// Breadcrumbs
// ===========
// This component extends and styles the breadcrump component.
//
// We support using <a> also as last, active element. We then automatically
// style it like an `active` element.

.breadcrumb {
  margin-left: -5px;
}

.breadcrumb-item {
  display: inline-flex;
  align-items: center;

  > a {
    display: inline-flex;
    align-items: center;
    color: $si-breadcrumb-color;

    &:hover {
      color: $si-breadcrumb-hover-color;
      text-decoration: none;
    }
  }

  &:last-child > a {
    color: $breadcrumb-active-color;
    font-weight: $breadcrumb-active-font-weight;
  }

  &.active > a {
    font-weight: $breadcrumb-active-font-weight;
  }

  + .breadcrumb-item::before {
    @include add-icon($siemens-arrow-right);
  }
}
