@import 'bootstrap/scss/progress';
// Range
// =====
// Slider control customized for Chrome/IE/Firefox
// Basic code/idea copied from https://css-tricks.com/styling-cross-browser-compatible-range-inputs-css/

@mixin track {
  width: $range-track-width;
  height: $range-track-height;
  cursor: pointer;
}
  
@mixin thumb {
  box-shadow: $range-thumb-box-shadow;
  border: $range-thumb-border-width solid $range-thumb-border-color;
  height: $range-thumb-height;
  width: $range-thumb-width;
  border-radius: $range-thumb-radius;
  background: $range-slider-bg;
  cursor: pointer;
}
  
input[type='range'] {
  appearance: none;
  width: $range-track-width;
  background: transparent;
  flex: 1;
  padding: $range-track-padding 0;
  
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ CSS hack */
    padding: 0;
  }
  margin: 0;
  
  &:focus {
    outline: none;
  }
  
  &::-webkit-slider-runnable-track {
    @include track;
    background: $range-track-color;
    border-radius: $range-track-radius;
    border: $range-track-border-width solid $range-track-border-color;
  }
  
  &::-webkit-slider-thumb {
    @include thumb;
    appearance: none;
    margin-top: ((-$range-track-border-width * 2 + $range-track-height) / 2) - ($range-thumb-height / 2);
  }
  
  &:focus::-webkit-slider-runnable-track {
    background: $range-track-focus-color;
  }
  
  &::-moz-range-track {
    @include track;
    background: $range-track-color;
    border-radius: $range-track-radius;
    border: $range-track-border-width solid $range-track-border-color;
  }
  
  &::-moz-range-thumb {
    @include thumb;
  }
  
  &:focus::-moz-range-track {
    background: $range-track-focus-color;
  }
  
  &::-ms-track {
    @include track;
    background: transparent;
    border-color: transparent;
    border-width: $range-thumb-width 0;
    color: transparent;
  }
  
  &::-ms-fill-lower {
    background: $range-track-color;
    border: $range-track-border-width solid $range-track-border-color;
    border-radius: $range-track-radius * 2;
  }
  
  &::-ms-fill-upper {
    background: $range-track-color;
    border: $range-track-border-width solid $range-track-border-color;
    border-radius: $range-track-radius * 2;
  }
  
  &::-ms-thumb {
    @include thumb;
  }
  
  &:focus::-ms-fill-lower {
    background: $range-track-color;
  }
  
  &:focus::-ms-fill-upper {
    background: $range-track-color;
  }
}
