// Bootstrap overrides
// ===================
// We adapt some bootstrap variables that should be picked up by Bootstrap
// and are global to all themes.
// All variables that needs to be overridden should be above bootstrap variables import.

$font-size-base: 1.1rem;
$font-family-sans-serif: 'Siemens Sans' !default;

$enable-shadows: false !default;
$input-focus-box-shadow: 0 !default;


// Color Systems
// -------------

$yiq-contrasted-threshold: 200 !default;

$dark: $si-deep-blue !default;
$light: $si-white !default;

$btn-padding-y: 15px;
$btn-padding-x: 50px;
$btn-font-size: 18px;

$input-transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, background 0.15s ease-in-out;


$theme-colors: () !default;
$theme-colors: map-merge(('inactive': #8b8b95,), $theme-colors);

// Animation Function
// ------------------
$si-linear-easing-function: cubic-bezier(0, 0, 1, 1) !default;
$si-snappy-easing-function: cubic-bezier(0.12, 0, 0.5, 1) !default;
$si-fluid-easing-function: cubic-bezier(0.66, 0, 0.1, 1) !default;


// Navs
// ----
$nav-link-color-hover: inherit !default;
$nav-link-padding-y: 0 !default;
$nav-link-padding-x: 0.9375rem !default;
$nav-link-padding: $nav-link-padding-y $nav-link-padding-x !default;
$nav-tabs-link-active-color: $si-nav-link-color !default;
$nav-tabs-link-active-bg: transparent !default;


// Breadcrumb
// ----------
$breadcrumb-bg: transparent !default;
$breadcrumb-padding-y: 20px !default;
$breadcrumb-padding-x: 0 !default;
$breadcrumb-active-color: $si-teal !default;

// Buttons
// --------
$btn-border-radius: 0 !default;
$btn-border-radius-lg: 0 !default;
$btn-border-radius-sm: 0 !default;
$btn-font-weight: middle !default;
$btn-border-radius-base: 0 !default;
$btn-outline: none !default;
$btn-focus-width: 0 !default;
$btn-focus-box-shadow: none !default;
$btn-active-box-shadow: none !default;
$btn-transition: color 0.15s $si-fluid-easing-function, background-color 0.15s $si-fluid-easing-function, border-color 0.15s $si-fluid-easing-function, box-shadow 0.15s $si-fluid-easing-function !default;

// Alerts
// -------
$alert-bg-level: 0 !default;
$alert-border-level: 0 !default;
$alert-padding-y: 1.375rem !default;
$alert-padding-x: 1.375rem !default;

// Components
// ----------
$border-radius: 0 !default;
$border-radius-lg: 0 !default;
$border-radius-sm: 0 !default;

// Cards
// -----------
$si-card-accordion-color: $body-color !default;
$card-border-width: 0 !default;
$card-bg: transparent !default;


// Typography
// ----------
$headings-font-weight: 900 !default;
$legend-font-size: $font-size-base * 1.5 !default;
$headings-color: null;
$paragraph-margin-bottom: 40px;

$h1-font-size: 60px;
$h2-font-size: 40px;
$h3-font-size: 36px;
$h4-font-size: 32px;
$h5-font-size: 24px;
$h6-font-size: 20px;
$lead-font-size: 18px;
$lead-font-weight: 900;
$blockquote-font-size: 48px;
$intro-font-size: 22px;
$intro-line-height: 32px;
$small-font-size: 12px;

// Jumbotron
// ---------
$jumbotron-padding: 10px !default;


// Forms
// ------
$form-group-margin-bottom: 20px !default;
$enable-validation-icons: false !default;
$form-feedback-font-size: 12px !default;
$form-feedback-margin-top: 5px !default;
$form-check-input-gutter: 0 !default;
$label-margin-bottom: 0 !default;
$input-height: 54px !default;
$input-padding-x: 1rem !default;
$input-disabled-bg: $input-bg;
$input-transition: border-color 0.15s $si-fluid-easing-function, background-color 0.15s $si-fluid-easing-function !default;


// Input groups
// ------------
$input-group-addon-color: $si-deep-blue;
$input-group-addon-bg: $si-bold-green;

// Grid
// ------
// adapted from brandville. No official spec
$container-max-widths: (
  sm: 540px + 30px,
  md: 750px + 30px,
  lg: 970px + 30px,
  xl: 1170px + 30px
) !default;

// List-group
// ----------
$list-group-item-padding-y: 24px;
$list-group-item-padding-x: 20px;
$list-group-border-width: null;

// Modal
$modal-inner-padding: 8px 32px 8px;
$modal-header-padding: 24px 32px 8px;
$modal-content-border-color: null !default;
$modal-content-border-width: 0 !default;
$modal-header-border-color: null !default;
$modal-header-border-width: 0 !default;
$modal-footer-border-color: null !default;
$modal-footer-border-width: 0 !default;

// Navbar
// ------------
$navbar-brand-padding-y: 0 !default;
$navbar-dark-brand-color: $si-white;
$navbar-light-brand-color: $si-deep-blue !default;
$navbar-padding-y: 0 !default;
$navbar-padding-x: 0 !default;
$navbar-dark-color: $sn-white !default;
$navbar-dark-hover-color: $si-white !default;
$navbar-light-color: $si-deep-blue !default;
$navbar-light-hover-color: $si-deep-blue !default;
$navbar-nav-link-padding-x: 0 !default;
$navbar-toggler-padding-y: 0 !default;
$navbar-toggler-padding-x: 0 !default;


// Pagination
$pagination-color: $link-color !default;
$pagination-hover-color: $link-color !default;
$pagination-active-color: $link-color !default;
$pagination-padding-y: 2px !default;
$pagination-padding-x: 10px !default;
$pagination-bg: transparent !default;
$pagination-border-color: transparent !default;
$pagination-border-width: 0 !default;
$pagination-hover-bg: $link-hover-color !default;
$pagination-hover-color: $si-white !default;
$pagination-active-bg: $link-hover-color !default;
$pagination-active-color: $si-white !default;
$pagination-color: $link-hover-color !default;
$pagination-focus-outline: 0 !default;
$pagination-focus-box-shadow: none !default;
$pagination-disabled-bg: transparent !default;

// Table
// ----
$si-table-cell-padding-x: 12px !default;
$si-table-cell-padding-y: 20px !default;
$table-cell-padding: $si-table-cell-padding-x $si-table-cell-padding-y !default;
$si-table-font-size: 16px !default;

// Dropdown
// --------
$dropdown-link-hover-color: $body-color !default;
$dropdown-header-padding: 10px 16px !default;
$dropdown-border-width: 0 !default;
$dropdown-spacer: 0 !default;
$dropdown-padding-y: 0 !default;
$si-dropdown-standalone-border: $dropdown-color !default;
$si-dropdown-triangle-height: 13px !default;
$si-dropdown-triangle-border-width: 2px !default;


// Toasts
// ------

$toast-max-width: auto !default;
$toast-border-radius: 0 !default;
$toast-border-width: 0 !default;
$toast-padding-y: 15px !default;
$toast-padding-x: 15px !default;

// Close
// -----
$close-text-shadow: none;
$close-color: $body-color;
$close-font-size: inherit;
$toast-font-size: inherit;

// Tooltip
$tooltip-opacity: 1 !default;
$tooltip-padding-x: 16px !default;
$tooltip-padding-y: 8px !default;
$tooltip-font-size: 12px !default;

// Import default variables from bootstrap to overwrite variables here.
@import 'bootstrap/scss/variables';
