@import 'bootstrap/scss/forms';

.form-check {
  padding-left: 0;
}

.form-check-inline .form-check-input {
  position: relative;
}

.form-feedback {
  padding-left: $input-padding-x;
  padding-right: $input-padding-x;
}

.form-control {
  @include focus-outline();
  padding-top: 19px;
  padding-bottom: 7px;
  border-bottom-width: 1px;

  &:hover,
  &:active {
    background-color: $si-input-bg-hover;
    border-color: $si-input-border-hover-color;
  }

  &:disabled {
    pointer-events: none;
    color: $si-input-color-disabled;
    border-bottom-width: 0;
  }

  &[readonly] {
    pointer-events: none;
    color: $si-input-color-disabled;
    background-color: transparent;
    border-bottom-width: 0;
  }
}


// Form Elements p.13
// ------------------------------
.form-control.dropdown-toggle {
  border-top-color: $input-border;
  border-bottom-color: $form-control-border-color-highlight;

  &:focus,
  &.focus {
    border-color: $form-control-border-color-focus;
  }

  &:hover {
    border-color: $form-control-border-color-hover;
  }
}

.form-group {
  position: relative;

  .dropdown-toggle {
    &::after {
      content: none;
    }
  }

  textarea.form-control {
    border-top-width: 20px;
    border-top-color: $input-bg;
    padding-top: 0;

    &:hover,
    &:active {
      border-top-color: $si-input-bg-hover;
    }

    &:focus {
      border-top-color: $input-bg;
      border-top-width: 20px;
      padding-top: 0;
    }

    &:focus:hover {
      border-top-color: $si-input-bg-hover;
      padding-top: 0;
    }
  }
}

.form-check,
.form-group {
  color: $si-form-group-label-color;
}

.form-group > label {
  position: absolute;
  left: 16px;
  top: 13px;
  z-index: 4;
  transition: top 0.1s, left 0.1s, font-size 0.15s $si-fluid-easing-function 0s;
  pointer-events: none;
}

.form-group.form-group-dropdown {
  .form-group-dropdown-value {
    background: transparent;
    border: 0;
    color: $sn-stone;
    left: 15px;
    margin: 0;
    outline: none;
    padding: 0;
    position: absolute;
    top: 20px;
    transition: top 0.1s, left 0.1s, font-size 0.15s $si-fluid-easing-function 0s;
    z-index: 1;
  }
}

.form-group > label.selected {
  top: 8px;
  left: 16px;
  font-size: 9px;
}

.form-group > label.disabled {
  color: $sn-searchbtn-color;
}


.was-validated .form-control:invalid,
.form-control.is-invalid {
  background: $si-input-bg-invalid;
}


.form-group select {
  padding-left: 11px;
}

.form-group .dropdown-toggle {
  text-align: left;
  padding-top: 22px;
  padding-bottom: 8px;
  padding-right: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.icon-dropdown {
  position: absolute;
  right: 15px;
  top: 26px;
  color: $dropdown-color;

  &::before {
    border-bottom: 7px solid;
    border-right: 7px solid transparent;
    content: '';
    position: absolute;
    transform: rotate(-45deg);
    right: 3px;
  }
}

button.form-control[disabled] {
  background-color: $form-control-disabled-bg;
  border-color: #cdd9e1;
  border-top-color: #879baa;
  color: $form-control-disabled-color;
}

button.form-control[disabled] + .icon-dropdown {
  color: $form-control-disabled-color;
}

button.focus + .icon-dropdown {
  &::before {
    transform: rotate(-225deg);
  }
}

button + .dropdown-menu,
.form-group .dropdown-menu {
  width: 100%;

  > a:not(.dropdown-divider),
  .dropdown-item {
    padding: 5px 8px 5px 18px;

    &:hover,
    &:focus {
      background-color: $sn-standard-linkbtn-activehover-color;
      cursor: pointer;

    }
  }
}

button + .dropdown-menu {
  > a:not(.dropdown-divider),
  .dropdown-item {
    padding-right: 29px;

    &:hover {
      padding-right: 15px;
    }
  }
}

.open > .btn-secondary.dropdown-toggle {
  background-color: darken($sn-standard-linkbtn-activehover-color, 10%);
  border-color: darken($sn-standard-linkbtn-activehover-color, 10%);
  color: $si-white;

  &:hover,
  &:focus {
    background-color: darken($sn-standard-linkbtn-activehover-color, 10%);
    border-color: darken($sn-standard-linkbtn-activehover-color, 10%);
    color: $si-white;
  }
}

%checkbox-radio-shared {
  display: inline-block;
  position: relative;
  cursor: pointer;
  appearance: none;
  background-color: $si-form-check-background;
  color: $si-form-check-color;
  border: 1px solid $si-form-check-color;

  &:checked {
    + label {
      color: $si-form-check-selected-label-color;
    }
  }

  &:hover {
    color: $si-form-check-hover-color;
    border-color: $si-form-check-hover-color;
    background: $si-form-check-hover-background;
  }

  &:disabled {
    color: $si-form-check-deactivated-color;
    border-color: $si-form-check-deactivated-color;
    background: transparent;

    &::after {
      color: inherit;
    }
  }

}


input[type='checkbox'] {
  @extend %checkbox-radio-shared;
  width: 21px;
  height: 21px;
  padding-right: 9px;
  border-radius: 0;
  margin: 1px 8px -5px 0;

  &:checked::after {
    @include add-icon($siemens-check);
    font-size: 18px;
    position: absolute;
    left: 1px;
    top: -4px;
  }

  &:indeterminate::after {
    @include add-icon($siemens-minus);
    position: absolute;
    font-size: 18px;
    left: 1px;
    top: -3px;

  }
}

input[type='radio'] {
  @extend %checkbox-radio-shared;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  padding-right: 9px;
  margin: 1px 8px -5px 0;

  &:checked::after {
    content: ' ';
    width: $sn-form-radio-bullet-size;
    height: $sn-form-radio-bullet-size;
    border-radius: 50px;
    position: absolute;
    top: $sn-form-radio-bullet-offset;
    left: $sn-form-radio-bullet-offset;
    background: $si-form-check-color;
    text-shadow: none;
    font-size: 32px;
  }

  &:hover::after {
    background: $si-form-check-hover-color;
  }
}
