@mixin si-button($theme, $hover-background) {
  .btn-#{$theme} {
    color: $body-bg;
    @if ($hover-background != null) {
      &:hover,
      &:focus {
        color: $body-bg;
        background: $hover-background;
        border-color: $hover-background;
      }
    }

    &:active:not(:disabled):not(.disabled),
    &:focus {
      color: $body-bg;
    }
  }
}

@mixin si-button-outline($theme, $hover-color) {
  .btn-outline-#{$theme}:not(:disabled):not(.disabled) {
    &:hover {
      color: $body-bg;
      background: $hover-color;
      border-color: $hover-color;
    }

    &:active {
      color: $body-bg;
    }
  }
}

@mixin button-circle-size($btn-size, $font-size) {
  display: inline-block;

  width: $btn-size;
  min-width: $btn-size;
  height: $btn-size;
  min-height: $btn-size;

  font-size: $font-size;
}
