@import 'bootstrap/scss/modal';
// Modal Dialogs
// =============
//
// This component extends styling options for modal dialogs.

// fix issues with jumping content
.modal-open {
  padding-right: 0 !important; // stylelint-disable-line
}

.modal-content {
  min-height: 270px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.modal-footer {
  padding: 8px 32px 24px;
}

