// Sidebar
// ======
//
// This component supports the main vertical navigation style / sidebar on Siemens application pages.
//
// The sidebar is based on the nav class and supports normal as well as off-canvas menus in
// light and inverse color scheme.

@mixin sidebar-item-color($element-bg, $element-link-color) {
  background-color: $element-bg;

  > a {
    color: $element-link-color;
  }
}

.sidebar {
  @extend .nav;

  background-color: $sidebar-bg;
  border-right: $sidebar-border-width solid $sidebar-border-color;
  font-weight: $headings-font-weight;
  width: $sidebar-width;
  height: 100%;
  overflow: hidden;

  > li {
    > a {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      padding: 20px 15px;

      span:first-child {
        margin-right: 10px;
      }

      &:hover,
      &:focus {
        background-color: $sidebar-hover-bg;
      }
    }

    &.active {
      @include sidebar-item-color($sidebar-active-bg, $sidebar-active-color);
    }

    &.nav-alternative {
      @include sidebar-item-color($sidebar-inverse-active-bg, $sidebar-inverse-active-color);

      > a:hover,
      > a:focus {
        background-color: $sidebar-inverse-hover-bg;
      }
    }
  }

  // Inverse / dark color pattern
  &.sidebar-inverse {
    background-color: $sidebar-inverse-bg;

    > li {
      > a {
        color: $sidebar-inverse-color;

        &:hover,
        &:focus {
          background-color: $sidebar-inverse-hover-bg;
        }
      }

      &.active {
        @include sidebar-item-color($sidebar-inverse-active-bg, $sidebar-inverse-active-color);
      }
    }
  }

  // Enables off-canvas functionality
  &.sidebar-offcanvas {
    max-width: 0;
    min-height: 100vh;
    transition: max-width 0.5s ease-in-out;

    &.open {
      max-width: $sidebar-width;
    }

    @media (min-width: map-get($grid-breakpoints , lg)) {
      max-width: $sidebar-width;
    }

    + .page-frame {
      @media (max-width: map-get($grid-breakpoints , md)) {
        box-shadow: $sidebar-box-shadow;
      }
    }
  }

  // Collapsible side menu
  &.sidebar-collapsible {
    transition: width 0.5s ease-in-out;

    @media (max-width: map-get($grid-breakpoints , xs)) {
      width: 50px;

      > li a {
        padding: 15px;

        .list-group-item-value {
          display: none;
        }
      }
    }
  }
}
