@import '~@simpl/siemens-brand-bootstrap/src/styles/siemens-colors';

$main-bg: $si-bold-green-12 !default;
$main-box-shadow: $si-bold-green-12 !default;
$font-color-01: $si-white; // header text color
$font-color-02: $si-deep-blue-70; // days of the week text
$font-color-03: $si-interactive-coral; // default text color

$highlighted: $si-interactive-coral-40;

$theme-list: () !default;

@import '~ngx-bootstrap/datepicker/bs-datepicker';
// names of ngx-bootstrap are a little misleading here: green is apparently the default theme (not default which exists as well!)
// resetting the theme-list an calling the theme mixing manually resolves the problem, that active and hover colors are not allowed to be overridden (missing !default)
// hopefully they get this fixed
@import './ngx-datepicker'; // style class for common styles

$theme-green: $si-bold-green-12;

@include theming('green', $theme-green);

.theme-green {
  .bs-datepicker-head {
    button.previous,
    button.next {
      color: $si-interactive-coral;
    }
  }

  .bs-datepicker-body {
    table {
      td {
        span.selected,
        &.selected span,
        span[class*='select-']::after,
        &[class*='select-'] span::after {
          background-color: $si-interactive-coral;
          color: $si-deep-blue;
        }

        span.is-other-month,
        &.is-other-month span {
          color: rgba($si-white, 0.25);
        }

        span.in-range {
          color: $si-deep-blue;
        }

        &:hover {
          color: $si-deep-blue;
        }

        &.week span {
          color: $si-white;
        }
      }
    }
  }
}
