// Siemens variables
// =================
// The following variables are defaults for our own components. This is not the
// place to override variables that come from bootstrap.

@import 'siemens-colors';

// Fonts / Icons
@import '@simpl/siemens-brand-icons/dist/style/variables';

$sn-icons-font-family: 'simpl-siemens-brand-icons' !default;

// Utils
// -----
// Specify custom location and filename of the included image assets.
// Useful for those including via Bower.
$image-path: '../assets/images/' !default;

// Set default icon font path according default simpl file layout.
// See https://code.siemens.com/simpl/simpl/blob/master/docs/app-file-structure.md
$icon-font-path: '../assets/fonts/' !default;


// Buttons
// -------


// TODO: Needs to be sorted
$sn-form-checkbox-checked-color: $si-deep-blue-70;
$sn-form-radio-bullet-size: 10px;
$sn-form-radio-bullet-offset: 4px;
$sn-form-checkbox-deactivated-color: $si-deep-blue-55;
$sn-form-checkbox-deactivated-border-color: $si-deep-blue-55;
$form-control-border-color-highlight: $si-deep-blue-70;
$input-border-width: 0;

$si-nav-brand-width: 120px !default;

// OLD NEWTON CODE


$gradient-default: 0% !default;

// Variables
// ------------
$font-family-serif: 'Siemens Serif' !default;
$padding-small-vertical: 0.3125rem !default;
$padding-small-horizontal: 0.625rem !default;
$padding-base-vertical: 6px !default;
$padding-base-horizontal: 12px !default;
$line-height-computed: floor(0.875rem * 1.5) !default;
$box-shadow-color: $sn-black-shadow !default;

// Petrol Gradient HTML/CSS definition
$sn-petrol-gradient: linear-gradient(to right,
$sn-gradient-color-left 0%,
$sn-gradient-color-middle-left 50%,
$sn-gradient-color-middle-right 83%,
$sn-gradient-color-right 100%) !default;

// Headlines
$sn-font-size-h1: 48px;
$sn-font-size-h1-mobile: 38px;
$sn-font-size-h2: 32px;
$sn-font-size-h3: 26px;
$sn-font-size-h4: 20px;
$sn-font-size-h5: 16px;
$margin-bottom-h1: 40px !default;
$margin-bottom-h2: 20px !default;

$sn-form-feedback-padding: 2px 15px; // differs from specs. But the specs would overflow subsequent elements

// page-frame
// ----------
$page-frame-border-size: 1px !default;
$page-frame-border-color: #ccc !default;

// Jumbotron
$jumbotron-color: $sn-white !default;
$jumbotron-hr-top-border-color: none !default;
$jumbotron-heading-font-size: $sn-font-size-h1 !default;


// Jumbotron-landing
// -----------------
$jumbotron-landing-image: '#{$image-path}brand-banner.png' !default;
$jumbotron-landing-height: 320px !default;
$jumbotron-landing-brand-height: 72px !default;
$jumbotron-landing-dropdown-width: 180px !default;
$jumbotron-landing-dropdown-color: $sn-white !default;
$jumbotron-landing-navbar-bg: #333 !default;
$jumbotron-landing-navbar-opacity: 1 !default;
$jumbotron-landing-dropdown-color-sm: $jumbotron-landing-dropdown-color !default;
$jumbotron-landing-color: #fff !default;
$jumbotron-landing-color-bg: rgba(0, 0, 0, 0.5) !default;

// Sidebar
// ---------------
$sidebar-width: 190px !default;
$sidebar-width: 142px !default;
$sidebar-bg: #c5d8e8 !default;
$sidebar-active-bg: #fff !default;
$sidebar-active-color: #00b1ff !default;
$sidebar-hover-bg: #e3ecf2 !default;

$sidebar-border-width: 1px !default;
