$sidebar-box-shadow: -5px 0 10px rgba(0, 0, 0, 0.75) !default;


// Dropdowns
$dropdown-top-padding: 2px !default;
$dropdown-selected-color: #dbe8f0 !default;
$dropdown-header-font-size: 1rem !default;
