$statusbar-text-color: $navbar-default-link-color !default;
$statusbar-padding-vertical: (
  ($statusbar-height - $line-height-computed) / 2
) !default;

$navbar-status-font-size: (0.875rem * 1.25) !default;
$navbar-status-title-font-size: (0.875rem * 0.875) !default;
$navbar-status-font-weight: inherit !default;
$navbar-status-space: $padding-small-vertical !default;
$navbar-status-border: $navbar-default-border !default;
$navbar-status-border-height: (
  $navbar-status-font-size + $navbar-status-title-font-size +
  $navbar-status-space
) !default;
$navbar-status-min-width: 50px !default;

// // nav-tabs
// // ----------------
$nav-tabs-font-weight: inherit !default;
$nav-tabs-text-align: left !default;

// nav-breadcumb
// -------------
