$range-thumb-width: $range-thumb-radius * 2 !default;
$range-thumb-border-width: 1px !default;
$range-thumb-border-color: $range-track-color !default;
$range-thumb-box-shadow: 2px 2px 5px $box-shadow-color !default;

// Filter Groups
// -------------
