.si-header {
  --si-header-offset-top: 60px;
  --navbar-expand: 0;

  > .container {
    display: grid;
    column-gap: 20px;
    padding: 15px $grid-gutter-width / 2;
  }
}

.si-header-internet > .container {
  grid-template-columns: 1fr auto auto;
  grid-template-areas: 'brand search toggler';
  grid-template-rows: 30px;
}

.si-header-extended {
  --si-header-offset-top: 110px;

  > .container {
    grid-template-columns: auto auto 1fr;
    grid-template-areas: 'brand brand nav-icons' '. . .' 'toggler search search';
    grid-template-rows: 28px 12px 40px;
  }
}

.si-header-intranet {
  .si-header-nav-icons {
    display: none;
  }

  > .container {
    grid-template-columns: auto auto 1fr auto;
    grid-template-rows: 30px;
    grid-template-areas: 'toggler intra . brand';
  }
}

.si-header-nav {
  display: flex;
  position: relative;
  flex-flow: column;
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}

%vertical-nav {
  flex-flow: row;
  gap: 15px;
}

.si-header-brand {
  display: flex;
  grid-area: brand;
}


.si-header-collapse {
  display: none;

  &.show {
    display: block;
  }
}

.si-header-nav,
.si-header-collapse {
  grid-area: nav;
}


%nav-menu {
  position: fixed;
  top: var(--si-header-offset-top);
  bottom: 0;
  right: 0;
  width: 100%;
  background-color: $si-navbar-background;
  justify-content: stretch;
  z-index: 1031;

  .si-header-link {
    padding: 9px 10px;

    &:hover {
      background-color: $si-nav-link-hover-background-color;
    }
  }

  > .container,
  > .si-header-collapse-wrapper > .container,
  > .si-header-dropdown-menu-wrapper > .container,
  > .si-header-collapse > .container-fluid,
  > .si-header-dropdown-menu-wrapper > .container-fluid,
  > .container-fluid {
    display: flex;
    gap: 45px;
    flex-flow: column;
    align-items: flex-start;
    justify-content: flex-start;
    height: 100%;
  }
}

.si-header-dropdown-menu {
  @extend %nav-menu;
}

.si-header-collapse {
  @extend %nav-menu;
}

.si-header-dropdown-nav {
  list-style: none;
  grid-area: dropdown-nav;
  padding-left: 0;
  width: 100%;
}

.si-header-dropdown-head {
  display: flex;
  grid-area: dropdown-head;
  flex-flow: column;
  width: 100%;

  .si-header-link-close {
    position: relative;

    &::before {
      @include add-icon($siemens-arrow-left);
      font-size: 25px;
      position: absolute;
      left: -15px;
    }
  }

  .si-header-link-current {
    color: $si-interactive-coral;
    font-weight: 900;
  }
}

.si-header-toggler {
  @extend .si-header-link;
  grid-area: toggler;
  width: fit-content;
  border: 0;
}

.si-header-search {
  grid-area: search;
  background: transparent;
  justify-self: end;
  border: 0;
  width: 100%;

  .form-control {
    height: 40px;
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 16px;
  }
}


.si-header-nav-main {
  @extend .si-header-nav;
  width: 100%;
}

.si-header-nav-additional {
  @extend .si-header-nav;
  width: 100%;
}

.si-header-nav-icons {
  @extend .si-header-nav;
  @extend %vertical-nav;
  grid-area: nav-icons;
  justify-self: right;
  align-self: center;
}

.si-header-platform {
  display: none;
  grid-area: platform;
}

.si-header-intranet-claim {
  grid-area: intra;
}

.si-header-link {
  color: $si-nav-link-color;
  display: block;
  white-space: nowrap;
  line-height: 28px;
  padding: 0;
  background: transparent;
  border: 0;
  width: 100%;
  text-align: left;

  &:hover {
    text-decoration: none;
  }
}

.si-header-dropdown-nav,
.si-header-nav-main, {
  .si-header-link {
    &:hover {
      color: $si-nav-link-color;
      background-color: $si-nav-link-hover-background-color;
    }
  }
}

.si-header-nav-additional {
  .si-header-link {
    color: $si-interactive-coral;

    &:hover {
      color: $si-interactive-coral;
    }
  }
}

.si-header-nav {
  .si-header-item {
    &:last-child {
      .si-header-link {
        margin-right: 0;
      }
    }
  }
}

.si-header-dropdown-toggle {
  display: flex;
  justify-content: space-between;

  &::after {
    @include add-icon($siemens-arrow-right);
    font-size: 25px;
  }
}

.si-header-dropdown-close {
  display: none;
  grid-area: dropdown-close;
}

svg * {
  fill: $si-brand-color;
}

.si-header-underline {
  display: none;
  border-top: 2px solid $si-interactive-coral;
  position: absolute;
  bottom: var(--si-header-underline-bottom, -15px);
  opacity: 1;
  transition: left 0.5s, width 0.5s, opacity 0.2s $si-snappy-easing-function 0.5s;
}

@mixin dual-row-header {
  padding-top: 10px;
  grid-template-rows: 50px 20px 40px;

  .si-header-nav-main .si-header-link {
    padding-bottom: 17px;
    margin-bottom: -17px;
  }
}

@mixin single-row-header {
  padding-top: 0;
  grid-template-rows: 100px;

  .si-header-nav-main .si-header-link {
    padding-bottom: 37px;
    margin-bottom: -37px;
  }
}

@mixin si-header-expand {
  --navbar-expand: 1;

  .si-header-toggler {
    display: none;
  }

  .si-header-collapse {
    position: static;
    background: transparent;
    display: block;
    align-self: end;

    .container,
    .container-fluid {
      display: flex;
      flex-flow: row;
      padding-right: 0;
      padding-left: 0;
    }

    .si-header-nav {
      flex-flow: row;
      gap: 15px;
    }

    .si-header-nav-additional {
      margin-left: auto;
      width: auto;

      .si-header-link {
        color: $si-nav-link-color;

        &:hover {
          color: $link-hover-color;
        }
      }
    }

    .si-header-nav-main {
      .si-header-link {
        font-weight: 900;

        &.si-header-server-underline {
          margin-bottom: -15px;
          padding-bottom: 13px;
          border-bottom: 2px solid $si-interactive-coral;
        }
      }
    }

    .si-header-link {
      padding: 0;

      &:hover {
        background: transparent;
      }

      &::after {
        content: '';
      }
    }
  }

  .si-header-dropdown-menu {
    background-color: $si-navbar-menu-expanded-background;

    .container,
    .container-fluid {
      display: grid;
      grid-template-columns: 300px 100px 400px auto;
      grid-template-rows: 46px auto;
      grid-template-areas: 'dropdown-close dropdown-close dropdown-close dropdown-close'
        'dropdown-head . dropdown-nav .';
      justify-content: stretch;
    }
  }

  .si-header-dropdown-close {
    display: block;
    padding: 0;
    justify-self: end;
    grid-area: dropdown-close;

    &:hover {
      background: transparent;
    }
  }

  .si-header-underline {
    display: block;
  }
}


@include media-breakpoint-up(md) {
  .si-header-internet {
    --si-header-offset-top: 120px;
    @include si-header-expand;

    > .container {
      row-gap: 10px;
      grid-template-rows: 40px 40px;
      grid-template-columns: 1fr auto;
      grid-template-areas: 'brand search' 'nav nav';
    }
  }

  .si-header-extended {
    --si-header-offset-top: 120px;

    > .container {
      grid-template-rows: 28px 40px;
      row-gap: 22px;
      grid-template-columns: auto auto 1fr;
      grid-template-areas: 'brand platform nav-icons' 'toggler . search';

      .si-header-search {
        width: 360px;
      }
    }
  }

  .si-header-intranet {
    --si-header-offset-top: 100px;

    > .container {
      grid-template-columns: auto auto auto 1fr auto auto;
      grid-template-rows: 70px;
      grid-template-areas: 'toggler intra platform . nav-icons brand';
      align-items: center;
    }

    .si-header-nav-icons {
      display: flex;
    }
  }

  .si-header-platform {
    display: inline;
  }
}

@include media-breakpoint-up(lg) {
  .si-header-internet {
    --si-header-offset-top: 100px;
    --si-header-underline-bottom: -36px;

    > .container {
      grid-template-columns: auto 1fr min-content;
      grid-template-rows: 70px;
      grid-template-areas: 'brand nav search';
    }

    .si-header-collapse {
      align-self: center;
    }
  }

  .si-header-extended {
    @include si-header-expand;

    > .container {

      grid-template-rows: 40px 40px;
      row-gap: 10px;
      grid-template-columns: auto 1fr auto 40px auto;
      grid-template-areas: 'brand platform search . nav-icons' 'nav nav nav nav nav';

      .si-header-search {
        width: 480px;
      }
    }


  }

}
