$font-path: '../fonts' !default;

@at-root {
  @font-face {
    font-family: 'simpl-siemens-brand-icons';
    src: url('#{$font-path}/simpl-siemens-brand-icons.woff2') format('woff2'),
      url('#{$font-path}/simpl-siemens-brand-icons.woff') format('woff'),
      url('#{$font-path}/simpl-siemens-brand-icons.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
}

[class^='siemens-'],
[class*=' siemens-'] {
  display: inline-block;
  font-family: 'simpl-siemens-brand-icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: middle;
  -webkit-font-smoothing: antialiased; // sass-lint:disable-line no-vendor-prefixes
  -moz-osx-font-smoothing: grayscale; // sass-lint:disable-line no-vendor-prefixes
}

